/* ========================================================================
   Component: init.less 初始化大体的布局
 ========================================================================== */
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: Microsoft YaHei, "Source Sans Pro", sans-serif;
  color: #363636;
}
h1,
h2,
h3,
h4 {
  font-weight: bold;
}
a {
  color: #363636;
}
a:hover {
  text-decoration: none;
  cursor: pointer;
}
ul,
ol {
  list-style-type: none;
}
li {
  list-style: none;
}
.wrapper {
  width: 100%;
  height: 1px;
  min-height: 100%;
  overflow-x: auto;
}
#header {
  position: fixed;
  width: 100%;
  height: 56px;
  background-color: #23b7e5;
}
#leftBar {
  position: fixed;
  width: 220px;
  height: 1px;
  min-height: 100%;
  top: 56px;
  left: 0;
  bottom: 0;
  background-color: #3a3f51;
}
#rightBar {
  width: 200px;
}
#content {
  position: absolute;
  left: 220px;
  top: 56px;
  right: 0;
  bottom: 60px;
  overflow: auto;
  background-color: rosybrown;
}
#footer {
  position: absolute;
  height: 60px;
  left: 220px;
  right: 0;
  bottom: 0;
  background-color: #888888;
}
body {
  background-color: #f5f7fa;
}
#nav {
  background-color: #337ab7;
}
